.header {
  position: absolute;
  transform: translateX(-50%);
  left: 50%;
  top: 70px;
  width: 85%;
  z-index: 50;

  @include mobile {
    top: 50px;
  }

  &__container {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include small-mobile {
      justify-content: flex-end;
      flex-wrap: wrap;
    }
  }

  &__logo {
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
    text-transform: uppercase;
    text-shadow: 0px 0px 4px rgba(255, 210, 210, 0.5);
    padding-right: 20px;

    @include mobile {
      font-size: 14px;
line-height: 17px;
    }

    @include small-mobile {
      margin-bottom: 20px;
    }
  }

  &__right {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &-contacts {
    display: flex;
    justify-content: space-between;
    align-items: center;

    &__order {
      margin-right: 55px;
      padding: 20px 40px;
      font-weight: normal;
      font-size: 18px;
      line-height: 1.22rem;
      text-transform: uppercase;

      @include big-tablet {
        margin-right: 25px;
      }

      @include tablet {
        font-size: 16px;
        padding: 14px 20px;
      }

      @include mobile {
        font-size: 14px;
        margin-right: 30px;
      }

      @media (max-width: 410px) {
        font-size: 10px;
        padding: 10px 15px;
        margin-right: 10px;
      }
    }

    &__phones {
      display: flex;
      align-items: center;
      margin-right: 100px;
      
      @include big-tablet {
        margin-right: 40px;
      }

      @include tablet {
        flex-direction: column;
      }
      
      @media (max-width: 440px) {
        margin-right: 20px;
      }
    }

    &__phone {
      display: block;
      text-transform: lowercase;
      font-size: 18px;
      line-height: 1.22rem;
      @include small-mobile {
        font-size: 14px;
      }
      &:first-child {
        margin-right: 30px;
        @include big-tablet {
          margin-right: 20px;
        }
        @include tablet {
          margin-right: 0;
        }
      }
    }
  }
}
