.burger-menu {
  margin: 0 auto;
  width: 70px;
  display: block;
  transition: all .3s;
  cursor: pointer;
  height: 22px;

  @include tablet {
    width: 50px;
    height: 15px;
  }

  
  @include small-mobile {
    width: 30px;
    height: 10px;
  }

  .bar {
    transition: all .3s;
    height: 3px;
    width: 100%;
    display: block;
    background-color: $whiteColor;
    border-radius: 10px;
  }

  .bar:nth-of-type(2) {
    margin: 22px 0 0;
    @include tablet {
      margin: 15px 0 0;
    }
    
    @include small-mobile {
      margin: 10px 0 0;
    }
  }

  &--closed {
    transition-delay: .3s;

    .bar {
      float: right;
    }

    .bar:nth-of-type(2) {
      width: 60%;
      transition-property: margin, height, width;
      transition-delay: .3s, .3s, 0s;
    }

    &:hover {
      .bar:nth-of-type(2) {
        width: 100%;
      }
    }
  }

  &--opened {
    padding-top: 12px;

    @include tablet {
      padding-top: 0;
    }

    .bar {
      width: 40px;
      @include tablet {
        width: 30px;
      }
      @include small-mobile {
        width: 20px;
      }
    }

    .bar:nth-of-type(1) {
      transform: rotate(45deg);
      transition-delay: .3s;
      height: 3px;
    }

    .bar:nth-of-type(2) {
      transform: rotate(-45deg);
      transition-delay: .3s;
      height: 3px;
      margin: 0;
      margin-top: -2px;
    }
  }
}
