.consultation {
  padding: 150px 0;

  @include mobile {
      padding: 100px 0;
  }

  &__items {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 50px;

    @include tablet {
        justify-content: center;
        margin-bottom: 50px;
        flex-wrap: wrap;
        text-align: center;
    }
  }

  &__item {
      max-width: 720px;
      &:first-child{
          margin-right: 50px;
          @include tablet {
              margin-right: 0;
              margin-bottom: 60px;
          }
      }
  }

  &__title {
    font-weight: 600;
    font-size: 48px;
    line-height: 59px;
    color: $mainColor;
    margin-bottom: 25px;

    @include big-tablet {
        font-size: 36px;
        line-height: 42px;
    }

    @include tablet {
        margin-bottom: 15px;
    }

    @media (max-width: 500px) {
        font-size: 28px;
        line-height: 32px;
    }
  }

  &__text {
    font-weight: normal;
    font-size: 36px;
    line-height: 150%;
    color: rgba(255, 255, 255, 0.7);
    
    @include big-tablet {
        font-size: 30px;
    }

    @media (max-width: 500px) {
        font-size: 22px;
    }
  }

  &__button {
    font-weight: normal;
    font-size: 36px;
    line-height: 44px;
    padding: 30px 55px;
      background: $mainColor;
      display: block;
      max-width: 500px;
      text-align: center;
      margin: 0 auto;
      
    @include big-tablet {
        font-size: 32px;
        line-height: 36px;
        max-width: 450px;
    }

    @include big-tablet {
        font-size: 24px;
        line-height: 30px;
        padding: 25px 40px;
    }

    @media (max-width: 500px) {
        font-size: 20px;
        line-height: 24px;
        padding: 20px 30px;
        max-width: 300px;
    }
  }
}
