@mixin desktop {
  @media (min-width: (1025px)) {
    @content;
  }
}

@mixin big-tablet {
  @media (max-width: (1220px)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: (1024px)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: (768px)) {
    @content;
  }
}

@mixin small-mobile {
  @media (max-width: (660px)) {
    @content;
  }
}

