
.popup {
    -webkit-overflow-scrolling:touch;
    z-index:100;
    padding:50px 10px;
    position:fixed;
    top:0;
    left:0;
    width:100%;
    height:100%;
    -webkit-transition:visibility .8s ease 0s;
    -o-transition:visibility .8s ease 0s;
    transition:visibility .8s ease 0s;
    visibility:hidden
   }
   .popup::before {
    content:"";
    background-color:rgba(0,0,0,.95);
    position:fixed;
    width:100%;
    height:100%;
    top:0;
    left:0;
    opacity:0;
    -webkit-transition:opacity .8s ease 0s;
    -o-transition:opacity .8s ease 0s;
    transition:opacity .8s ease 0s
   }
   .popup.show {
    visibility:visible;
    overflow:auto
   }
   .popup.show::before {
    opacity:1
   }
   .popup.show .popup__body {
    -webkit-transform:scale(1);
    -ms-transform:scale(1);
    transform:scale(1)
   }
   .popup._active {
    overflow:auto;
    visibility:visible;
   }
   .popup._active::before {
    opacity:1
   }
   .popup._active .popup__body {
    -webkit-transition:all .3s ease .2s;
    -o-transition:all .3s ease .2s;
    transition:all .3s ease .2s;
    -webkit-transform:scale(1);
    -ms-transform:scale(1);
    transform:scale(1)
   }
   .popup__wrapper {
    padding-top:55px
   }
   .popup__content {
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;
    -webkit-box-orient:vertical;
    -webkit-box-direction:normal;
    -ms-flex-direction:column;
    flex-direction:column;
    -webkit-box-align:center;
    -ms-flex-align:center;
    align-items:center;
    -webkit-box-flex:1;
    -ms-flex:1 1 auto;
    flex:1 1 auto;
    width:100%;
    display:flex;
    -webkit-box-pack:center;
    -ms-flex-pack:center;
    justify-content:center;
    align-items:center
   }
   .popup__body {
    -webkit-transform:scale(0);
    -ms-transform:scale(0);
    transform:scale(0);
    -webkit-transition:all .3s ease 0s;
    -o-transition:all .3s ease 0s;
    transition:all .3s ease 0s;
    padding:20px;
    width:100%;
    max-width:560px;
    max-height:100vh
   }
   .popup__close {
    width:20px;
    height:20px;
    position:absolute;
    top:20px;
    right:20px;
    cursor:pointer;
    z-index:30;
    background:url(../img/close.svg) 0 0 no-repeat;
   }
   .popup__title {
    color:#faebd7;
    font-size:36px;
    font-weight:800;
    margin-bottom:30px
   }
   .popup__text {
    color:#3a3a3a;
    font-size:24px
   }
   .popup__form {
    max-width:550px;
    margin:0 auto;
    color:#fff;
    @include mobile {
        max-width: 350px;
    }
   }
   .form * {
    outline:0
   }
   .popup__form-body {
    position:relative
   }
   .popup__form-body::after {
    content:"";
    position:absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    opacity:0;
    visibility:hidden;
    -webkit-transition:all .5s ease 0s;
    -o-transition:all .5s ease 0s;
    transition:all .5s ease 0s
   }
   .popup__form-body._sending::after {
    opacity:1;
    visibility:visible
   }
   .popup__form-title {
    font-size:40px;
    font-weight:700;
    margin:0 0 50px 0;
    color:#fff;
    line-height:2.2rem;

    @include mobile {
        font-size: 32px;
        margin: 0 0 30px 0;
    }
   }
   .popup__form-text {
    color:#f0f8ff
   }
   .popup__form-item {
    margin:0 0 30px 0;
    @include mobile {
        margin: 0 0 20px 0;
    }
   }
   .popup__form-label {
    font-size:24px;
    display:block;
    margin:0 0 10px 0;

    @include mobile {
        font-size: 20px;
    }
   }
   .popup__form-input {
    height:40px;
    padding:0 15px;
    border-radius:5px;
    width:100%;
    font-size:22px;
    -webkit-transition:all .5s ease 0s;
    -o-transition:all .5s ease 0s;
    transition:all .5s ease 0s;
    font-family:inherit;
    color: $blueColor;
   }
   .popup__form-input:focus {
    -webkit-box-shadow:0 0 15px #0B1D33;
    box-shadow:0 0 15px #0B1D33
   }
   .popup__form-input._error {
    -webkit-box-shadow:0 0 15px red;
    box-shadow:0 0 15px red
   }
   .popup__form-error {
    margin-top:10px;
    font-weight:400;
    font-size:20px;
    line-height:21px;
    color:red
   }
   textarea.popup__form-input {
    min-height:80px;
    resize:vertical;
    padding:10px;
    max-height: 250px;
   }
   .options {
    padding:10px 0 0 0
   }
   .options__item {
    margin:0 0 10px 0
   }
   .options__input {
    display:none
   }
   .options__input:checked+.options__label::after {
    -webkit-transform:scale(1);
    -ms-transform:scale(1);
    transform:scale(1)
   }
   .options__label {
    display:-webkit-inline-box;
    display:-ms-inline-flexbox;
    display:inline-flex;
    font-size:16px;
    line-height:140%;
    -webkit-box-align:center;
    -ms-flex-align:center;
    align-items:center;
    position:relative;
    cursor:pointer
   }
   .options__label::before {
    content:"";
    -ms-flex-item-align:start;
    align-self:flex-start;
    -webkit-box-flex:0;
    -ms-flex:0 0 24px;
    flex:0 0 24px;
    height:24px;
    background-color:#fff;
    border-radius:50%;
    margin:0 10px 0 0
   }
   .options__label::after {
    -webkit-transition:-webkit-transform .5s ease 0s;
    transition:-webkit-transform .5s ease 0s;
    -o-transition:transform .5s ease 0s;
    transition:transform .5s ease 0s;
    transition:transform .5s ease 0s,-webkit-transform .5s ease 0s;
    content:"";
    position:absolute;
    top:4px;
    left:4px;
    width:16px;
    height:16px;
    background-color:#3f6745;
    border-radius:50%;
    -webkit-transform:scale(0);
    -ms-transform:scale(0);
    transform:scale(0)
   }
   .checkbox._error .checkbox__label::before {
    -webkit-box-shadow:0 0 15px red;
    box-shadow:0 0 15px red
   }
   .checkbox__input {
    display:none
   }
   .checkbox__input:checked+.checkbox__label::after {
    -webkit-transform:scale(1);
    -ms-transform:scale(1);
    transform:scale(1)
   }
   .checkbox__label {
    font-size:18px;
    line-height:1.2rem;
    display:-webkit-inline-box;
    display:-ms-inline-flexbox;
    display:inline-flex;
    -webkit-box-align:center;
    -ms-flex-align:center;
    align-items:center;
    position:relative;
    cursor:pointer;

    @include mobile {
        font-size: 14px;
    }
   }
   .checkbox__label::before {
    content:"";
    -ms-flex-item-align:start;
    align-self:flex-start;
    -webkit-box-flex:0;
    -ms-flex:0 0 20px;
    flex:0 0 20px;
    height:20px;
    background-color:#fff;
    border-radius:4px;
    margin:0 10px 0 0
   }
   .checkbox__label::after {
    -webkit-transition:-webkit-transform .5s ease 0s;
    transition:-webkit-transform .5s ease 0s;
    -o-transition:transform .5s ease 0s;
    transition:transform .5s ease 0s;
    transition:transform .5s ease 0s,-webkit-transform .5s ease 0s;
    content:"";
    width:12px;
    height:12px;
    position:absolute;
    top:4px;
    left:4px;
    background-color: $mainColor;
    border-radius:4px;
    -webkit-transform:scale(0);
    -ms-transform:scale(0);
    transform:scale(0)
   }
   .checkbox__label a {
    color: $mainColor;
   }
   .popup__form-button {
    width:100%;
    display:flex;
    justify-content:center;
    align-items:center;
    min-height:60px;
    background-color:$mainColor;
    color:#fff;
    font-size:20px;
    text-transform:uppercase;
    letter-spacing:2px;
    font-weight:600;
  border: 1px solid $mainColor;
  filter: drop-shadow(0px 0px 10px $mainColor);
  border-radius: 12px;
  white-space: nowrap;
    cursor:pointer;
    -webkit-transition:background-color .5s ease 0s;
    -o-transition:background-color .5s ease 0s;
    transition:background-color .5s ease 0s;
    position:relative;
    top:0
   }
   .popup__form-button:active {
    top:3px;
   }