html {
  box-sizing: border-box;
  background-color: $blueColor;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  margin: 0;
  font-family: 'Montserrat', 'Roboto', sans-serif;
  font-display: swap;
  color: $whiteColor;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

a,
button,
input,
textarea {
  color: inherit;
  font-family: inherit;
}

h1, h2, h3, h4, h5 {
  display: block;
}

.container {
  margin: 0 auto;
  // padding: 0 15px;
  max-width: 85%;
}

.site-container {
  position: relative;
}

.btn {
  border: 1px solid $mainColor;
  filter: drop-shadow(0px 0px 10px $mainColor);
  border-radius: 12px;
  white-space: nowrap;
}
