.hero {
  background: url('../img/hero-bg.jpg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 40% 50%;
  position: relative;
  padding: 300px 0 140px;

  @include mobile {
    padding: 250px 0 100px;
  }

  &::after {
    position: absolute;
    content: '';
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    background: rgba(11, 29, 51, 0.4);
  }

  @include desktop {
    min-height: 100vh;
  }

  &__container {
    position: relative;
    z-index: 10;
    margin: 0 auto;
    text-align: center;
  }

  &__content {
    margin-bottom: 70px;

    @include tablet {
      margin-bottom: 60px;
    }
  }

  &__subtitle {
    font-weight: 500;
    font-size: 48px;
    line-height: 59px;
    text-transform: uppercase;
    margin-bottom: 25px;

    @include tablet {
      font-size: 36px;
      line-height: 42px;
    }

    @include mobile {
      font-size: 24px;
      line-height: 29px;
    }

    @include small-mobile {
        font-size: 18px;
        line-height: 22px;
        margin-bottom: 15px;
    }
  }

  &__title {
    font-weight: bold;
    font-size: 72px;
    line-height: 87px;
    text-transform: uppercase;
    margin-bottom: 95px;

    @include tablet {
      font-size: 56px;
      line-height: 64px;
    }

    @include mobile {
      font-size: 48px;
      line-height: 54px;
    }

    @include small-mobile {
        font-size: 28px;
        line-height: 34px;
        margin-bottom: 60px;
    }
  }

  &__order {
    font-size: 24px;
    text-transform: uppercase;
    padding: 36px 90px;
    background: $mainColor;
    display: block;
    max-width: 430px;
    max-height: 100px;
    margin: 0 auto;

    @include tablet {
      padding: 30px 70px;
    }

    @include small-mobile {
        padding: 20px 30px;
        max-width: 300px;
        font-size: 20px;
    }
  }

  &-advantages {
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: baseline;

    
    @include tablet {
        flex-wrap: wrap;
    }

    &__item {
      margin: 0 auto;
      width: 390px;

      @include tablet {
          width: 220px;
      }

      &:not(:last-child) {
        @media (max-width: 510px) {
            margin-bottom: 20px;
            width: 300px;
        }
      }
    }

    &__icon {
      margin-bottom: 30px;
      svg {
          width: 100%;
          max-width: 80px;
          max-height: 80px;
      }

      @include tablet {
          max-width: 45px;
          max-height: 45px;
          margin: 0 auto 16px;
      }
    }

    &__text {
      font-weight: bold;
      font-size: 20px;
      line-height: 1.6rem;
      text-align: center;
      letter-spacing: 0.15em;
      text-transform: uppercase;

      @include tablet {
        font-size: 11.3422px;
      }
    }
  }
}
